















































import getRepository from '@/services'
import { Component, Mixins } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import DistrictMixin from '@/mixins/commonMixin'
import OfferRepository from '@/services/repositories/offer'
import FilterOffer from '../components/filter/Filter.vue'
import PopupOffer from '../components/popup/PopupOffer.vue'
import PopupConfirm from '../components/popup/PopupConfirm.vue'

@Component({
  components: { FilterOffer, PopupOffer, PopupConfirm }
})
export default class OfferProgram extends Mixins(PopupMixin, DistrictMixin) {
  apiOffer?: OfferRepository
  params: Record<string, any> = {
    search: '',
    page: 1,
    limit: 20,
    total: 0
  }

  isConflickClick = false
  isLoading = false
  data: Array<Record<string, any>> = []
  detailRow: Record<string, any> = {}
  type = 'create'

  created(): void {
    this.apiOffer = getRepository('offer', {
      districtId: this.districtId
    })
    this.init()
  }

  get getRoleOffer(): boolean {
    return this.checkPemission('offer', ['add-offer'])
  }

  indexMethod(index: number): number {
    return (this.params.page - 1) * this.params.limit + index + 1
  }

  handleSizeChange(value: number): void {
    this.params.page = 1
    this.params.limit = value
    this.init()
  }
  handleCurrentChange(value: number): void {
    this.params.page = value
    this.init()
  }

  handleRowClick(data: { row: Record<string, any> }): void {
    if (this.getRoleOffer) {
      if (this.isConflickClick) {
        this.isConflickClick = false
        return
      }
      this.handleEdit(data.row)
      this.isConflickClick = false
    }
  }

  handleSearch(value: string): void {
    this.params.page = 1
    this.params.search = value
    this.init()
  }

  handleOpenPopupCreate(): void {
    this.type = 'create'
    this.setOpenPopup({
      popupName: 'popup-offer',
      isOpen: true
    })
  }

  handleCreate(data: Record<string, any>): void {
    this.apiOffer
      ?.createOffer(data)
      .then(() => {
        this.$message.success('Tạo chương trình ưu đãi thành công')
        this.setOpenPopup({
          popupName: 'popup-offer',
          isOpen: false
        })
        this.init()
      })
      .catch(error => {
        if (error.errorCode === 500 && error.message === 'Mã chương trình đã tồn tại') {
          this.$message.error(error.message)
        }
      })
  }

  handleUpdate(data: Record<string, any>): void {
    this.apiOffer
      ?.updateOffer(data)
      .then(() => {
        this.$message.success('Sửa chương trình ưu đãi thành công')
        this.setOpenPopup({
          popupName: 'popup-offer',
          isOpen: false
        })
        this.init()
      })
      .catch(error => {
        if (error.errorCode === 500 && error.message === 'Mã chương trình đã tồn tại') {
          this.$message.error(error.message)
        }
      })
  }
  handleDelete(id: number): void {
    this.apiOffer
      ?.deleteOffer(id)
      .then(() => {
        this.$message.success('Xóa chương trình ưu đãi thành công')
        this.init()
      })
      .catch(() => {
        this.$message.error('Xóa chương trình ưu đãi thất bại')
      })
  }

  handleOpenConfirm(row: Record<string, any>): void {
    this.isConflickClick = true
    this.detailRow = row
    this.setOpenPopup({
      popupName: 'popup-confirm-offer',
      isOpen: true
    })
  }

  handleConfirm(): void {
    this.handleDelete(this.detailRow.id)
    this.setOpenPopup({
      popupName: 'popup-offer',
      isOpen: false
    })
  }
  async init(): Promise<void> {
    try {
      this.isLoading = true
      const params = {
        ...this.params,
        search: this.params.search || null,
        total: null
      }
      const res = await this.apiOffer?.getOffer(params)
      this.data = res.content || []
      this.params.total = res.totalElements
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
      console.log(error)
    }
  }

  handleEdit(row: Record<string, any>): void {
    this.isConflickClick = true
    this.detailRow = row
    this.type = 'edit'
    this.setOpenPopup({
      popupName: 'popup-offer',
      isOpen: true
    })
  }
}
