














import { Component, Vue, Watch } from 'vue-property-decorator'
import { debounce, trim } from 'lodash'
@Component
export default class FilterOffer extends Vue {
  search = ''
  @Watch('search') handleSearch(key: string): void {
    this.searchText(key)
  }
  handleCreate(): void {
    this.$emit('create')
  }
  searchText = debounce((value: string) => {
    this.$emit('search', trim(value))
  }, 500)
}
