






























import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import { trim } from 'lodash'

@Component
export default class PopupOffer extends Mixins(PopupMixin) {
  @Prop({
    required: true,
    type: Object,
    default: () => {
      return {}
    }
  })
  detail!: Record<string, any>
  @Prop({ required: false, type: String, default: 'create' }) type!: string
  data: Record<string, any> = {}

  errorName = false
  errorCode = false

  isExistContact = false

  optionType: Array<Record<string, any>> = []

  handleOpen(): void {
    this.errorName = false
    this.errorCode = false
    if (this.type === 'edit') {
      this.data = { ...this.detail }
    } else {
      this.data = {}
    }
  }

  handleChangeInput(type: string): void {
    console.log(type)

    if (type === 'name') {
      this.errorName = false
    } else {
      this.errorCode = false
    }
    this.$forceUpdate()
  }

  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'popup-offer',
      isOpen: false
    })
  }

  handleSave(): void {
    if (trim(this.data.programName) === '') {
      this.errorName = true
    }
    if (trim(this.data.programCode) === '') {
      this.errorCode = true
    }
    if (!this.errorName && !this.errorCode) {
      if (this.type === 'create') {
        this.$emit('create', this.data)
      } else {
        this.$emit('update', this.data)
      }
    }
  }
  handleDelete(): void {
    this.$emit('delete', this.data)
  }
}
