


















import { Component, Mixins } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import DistrictMixin from '@/mixins/commonMixin'

@Component({ components: {} })
export default class PopupConfirm extends Mixins(PopupMixin, DistrictMixin) {
  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'popup-confirm-offer',
      isOpen: false
    })
  }
  handleConfirm(): void {
    this.handleCancel()
    this.$emit('confirm')
  }
  handleOpen(): void {
    console.log('open')
  }
  handleClose(): void {
    console.log('close')
  }
}
